/** @prettier */

// Import vue component
import FoldBox from '@components/FoldBox.vue';
import FoldHandle from '@components/FoldHandle.vue';
import FoldContent from '@components/FoldContent.vue';

// Declare install function executed by Vue.use()
export function install(Vue) {
  if (install.installed) {
    return;
  }
  install.installed = true;
  Vue.component('fold-box', FoldBox);
  Vue.component('fold-handle', FoldHandle);
  Vue.component('fold-content', FoldContent);
}

// Create module definition for Vue.use()
const plugin = {
  install
};

// Auto-install when vue is found (eg. in browser via <script> tag)
let GlobalVue = null;
if (typeof window !== 'undefined') {
  GlobalVue = window.Vue;
} else if (typeof global !== 'undefined') {
  GlobalVue = global.Vue;
}
if (GlobalVue) {
  GlobalVue.use(plugin);
}

// To allow use as module (npm/webpack/etc.) export component
export { FoldBox, FoldHandle, FoldContent };
